// colors //

$dark: black;
$light: white;
$gold: #FFC619;
$primary: #001A49;
$dark-success: #134E42;
$light-success: rgba(30, 167, 139, 0.2);

.FreeCourses {
    padding-top: 8vw !important;

    @media (max-width:1000px) {
        padding-top: 10vw !important;
    }

    @media (max-width:767px) {
        padding-top: 16vw !important;
    }

    @media (max-width:450px) {
        padding-top: 20vw !important;
    }

    .wrapper {
        display: flex;
        flex-direction: column;

        .main-video {
            width: 100%;
            height: 35vw;
            position: relative;

            @media (max-width:767px) {
                height: 52vw;
            }

            .contr {
                top: 0;
                left: 0;
                z-index: 2;
                width: 100%;
                height: 100%;
                position: absolute;
                background-color: transparent;
            }

            iframe {
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                position: absolute;
            }
        }

        .texts {
            display: flex;
            padding: 0 11vw;
            flex-direction: column;

            @media (max-width:767px) {
                padding: 0;
            }

            .big-text {
                font-weight: 400;
                color: $dark-success;
                font-family: mulish400;
            }
        }

        .forms {
            display: flex;
            align-items: center;
            flex-direction: column;

            .input {
                width: 100%;
                outline: none;
                background-color: $light;
                border: solid 1px $dark-success;
                box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;

                &:focus {
                    border-color: $dark;
                }
            }

            input::-webkit-inner-spin-button,
            input::-webkit-outer-spin-button {
                margin: 0;
                appearance: none;
                -webkit-appearance: none;
            }

            input[type=number] {
                appearance: 0;
                -moz-appearance: 0;
            }

            .send-btn {
                border: none;
                color: $light;
                font-weight: 700;
                padding: 1.5vw 4vw;
                font-family: mulish700;
                background-color: $dark-success;

                @media (max-width:767px) {
                    padding: 3vw 7vw;
                }
            }
        }

        .btns {
            display: flex;
            align-items: center;
            flex-direction: column;
        }

        .explore-bg {
            gap: 5px;
            border: none;
            outline: none;
            display: flex;
            align-items: center;
            text-decoration: none;
            justify-content: center;
            color: $light !important;
            font-weight: 700 !important;
            background-color: $dark-success;
            font-family: mulish700 !important;
        }
    }
}