// colors //

$dark: black;
$light: white;
$gold: #FFC619;
$primary: #001A49;
$dark-success: #134E42;
$light-success: rgba(30, 167, 139, 0.2);

.HomeCourses {
    .wrapper {
        display: flex;
        padding-top: 0;
        flex-direction: column;

        .sub-title {
            font-weight: 700;
            text-align: center;
            font-family: mulish700;
        }

        .mySwiper {
            width: 100%;
            padding: 0 20px;
            padding-bottom: 3vw;
            align-items: center;

            @media (max-width:767px) {
                padding-bottom: 7vw;
            }

            @media (max-width:450px) {
                padding-bottom: 9vw;
            }

            .swiper-wrapper {
                align-items: center;
            }

            .course {
                display: flex;
                align-items: center;
                justify-content: space-between;
                background-color: $dark-success;

                .infos {
                    width: 60%;
                    height: 100%;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;

                    @media (max-width:450px) {
                        gap: 2vw;
                        width: 100%;
                    }

                    .top {
                        display: flex;
                        align-items: center;

                        @media (max-width:767px) {
                            align-items: flex-start;
                            flex-direction: column-reverse;
                        }

                        .min-text {
                            color: $light;
                            font-family: mulish400;
                        }

                        .stamp {
                            gap: 5px;
                            display: flex;
                            font-weight: 700;
                            padding: 5px 12px;
                            align-items: center;
                            border-radius: 50px;
                            -webkit-border-radius: 50px;
                            -moz-border-radius: 50px;
                            -ms-border-radius: 50px;
                            -o-border-radius: 50px;
                            color: $dark-success;
                            font-family: mulish700;
                            justify-content: center;
                            background-color: $gold;

                            .icon {
                                width: 0.6vw;
                                display: flex;
                                fill: $dark-success;
                                align-items: center;
                                justify-content: center;

                                @media (max-width:767px) {
                                    width: 1.5vw;
                                }

                                @media (max-width:450px) {
                                    width: 2vw;
                                }
                            }
                        }
                    }

                    .middle {
                        display: flex;
                        flex-direction: column;

                        .name {
                            color: $light;
                            text-align: left;
                            font-weight: 700;
                            font-family: mulish700;
                        }

                        .min-text {
                            opacity: 0.9;
                            color: $light;
                            line-height: 1.5vw;

                            @media (max-width:767px) {
                                line-height: 2.5vw;
                            }

                            @media (max-width:450px) {
                                line-height: 4vw;
                            }
                        }
                    }

                    .bottom {
                        display: flex;
                        align-items: center;

                        .explore {
                            font-weight: 500;
                            padding: 0.7vw 1.5vw;
                            color: $dark-success;
                            font-family: mulish400;
                            background-color: $light;
                            border: solid 1px $light;

                            &:last-child {
                                color: $light;
                                background-color: transparent;
                            }

                            @media (max-width:450px) {
                                padding: 1.5vw 3vw;
                            }
                        }
                    }
                }

                .img {
                    max-width: 32%;
                    min-width: 32%;

                    @media (max-width:450px) {
                        display: none;
                    }
                }
            }

            .swiper-button-next,
            .swiper-button-prev {
                right: 0;
                top: 45%;
                width: 3vw;
                height: 3vw;
                display: flex;
                border-radius: 50%;
                -webkit-border-radius: 50%;
                -moz-border-radius: 50%;
                -ms-border-radius: 50%;
                -o-border-radius: 50%;
                align-items: center;
                justify-content: center;
                background-color: $light;
                border: solid 2px rgb(43, 43, 43);

                &:hover {
                    opacity: 0.8;
                }

                &::after {
                    font-size: 1.5vw;
                    color: rgb(43, 43, 43);

                    @media (max-width:767px) {
                        font-size: 2.5vw;
                    }
                }

                @media (max-width:1000px){
                    top: 50%;
                }

                @media (max-width:767px) {
                    top: 45%;
                    width: 6vw;
                    height: 6vw;
                    padding: 1vw;
                    border-width: 1px;
                }

                @media (max-width:450px) {
                    width: 7vw;
                    height: 7vw;
                    padding: 1vw;
                    border-width: 1px;
                }
            }

            .swiper-button-prev {
                left: 0;
                right: auto;
            }

            .swiper-pagination {
                span {
                    width: 0.8vw;
                    height: 0.8vw;
                    background-color: $dark-success;

                    @media (max-width:767px) {
                        width: 1.5vw;
                        height: 1.5vw;
                    }

                    @media (max-width:450px) {
                        width: 2vw;
                        height: 2vw;
                    }
                }
            }
        }
    }
}