// colors //

$dark: black;
$light: white;
$primary: #001A49;
$dark-success: #134E42;
$light-success: rgba(30, 167, 139, 0.2);

.Comments {
    .wrapper {
        display: flex;
        flex-direction: column;

        .sub-title {
            font-weight: 700;
            font-family: mulish700;
        }

        .comment-text {
            width: 50%;

            @media (max-width:767px) {
                width: 100%;
            }
        }
    }

    .mySwiper {
        width: 100%;
        padding: 0 30px;

        .comment {
            display: flex;
            flex-direction: column;
            background-color: $dark-success;

            .desc {
                opacity: 0.8;
                color: $light;
                overflow: hidden;
                display: -webkit-box;
                -webkit-line-clamp: 4;
                -webkit-box-orient: vertical;
            }

            .bottom {
                display: flex;
                align-items: center;

                .img {
                    max-width: 5vw;
                    min-width: 5vw;
                    border-radius: 50%;
                    -webkit-border-radius: 50%;
                    -moz-border-radius: 50%;
                    -ms-border-radius: 50%;
                    -o-border-radius: 50%;

                    @media (max-width:767px) {
                        max-width: 8vw;
                        min-width: 8vw;
                    }

                    @media (max-width:450px) {
                        max-width: 12vw;
                        min-width: 12vw;
                    }
                }

                .texts {
                    display: flex;
                    flex-direction: column;

                    .big-text {
                        color: $light;
                        font-family: mulish700;
                    }

                    .text {
                        opacity: 0.8;
                        color: $light;
                    }
                }
            }
        }
    }
}