// colors //

$dark: black;
$light: white;
$gold: #FFC619;
$primary: #001A49;
$dark-success: #134E42;
$light-success: rgba(30, 167, 139, 0.2);

.Contacts {
    background-color: $dark-success;

    .wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @media (max-width:767px) {
            gap: 3vw;
            flex-direction: column;
        }

        .left {
            width: 43%;
            display: flex;
            flex-direction: column;

            @media (max-width:767px) {
                width: 100%;
            }

            .sub-title {
                color: $light;
                font-weight: 700;
                font-family: mulish700;
            }

            .text {
                opacity: 0.8;
                color: $light;
            }
        }

        .right {
            width: 55%;
            display: grid;
            position: relative;
            grid-template-columns: repeat(2, 2fr);

            @media (max-width:767px) {
                width: 100%;
            }

            @media (max-width:450px) {
                grid-template-columns: repeat(1, 1fr);
            }

            .inputs {
                width: 100%;
                display: flex;
                flex-direction: column;
                justify-content: space-between;

                .tool {
                    position: relative;
                }
            }

            .textarea {
                width: 100%;
                position: relative;

                textarea {
                    resize: none;
                    height: 100%;
                }
            }

            label {
                color: $light;
                margin-left: 1vw;
                margin-top: 0.5vw;
                position: absolute;

                @media (max-width:1000px) {
                    margin-left: 1.5vw;
                }

                @media (max-width:767px) {
                    margin-top: 2vw;
                    margin-left: 2.5vw;
                }
            }

            .form-input {
                width: 100%;
                border: none;
                outline: none;
                color: $light;
                padding: 0.9vw;
                padding-top: 2.3vw;
                background-color: #ffffff1b;

                @media(max-width:1000px) {
                    padding: 1.5vw;
                    padding-top: 4vw;
                }

                @media (max-width:767px) {
                    padding: 2.5vw;
                    padding-top: 5.5vw;
                }

                @media (max-width:450px) {
                    padding-top: 6.5vw;
                }

                &::placeholder {
                    color: $light;
                    opacity: 0.35;
                }
            }

            input::-webkit-inner-spin-button,
            input::-webkit-outer-spin-button {
                margin: 0;
                appearance: none;
                -webkit-appearance: none;
            }

            input[type=number] {
                appearance: 0;
                -moz-appearance: 0;
            }

            .send-btn {
                right: 0;
                bottom: -1vw;
                border: none;
                font-weight: 600;
                position: absolute;
                padding: 0.7vw 1.5vw;
                color: $dark-success;
                font-family: mulish700;
                background-color: $light;

                @media (max-width:767px) {
                    bottom: -1.5vw;
                    padding: 1.5vw 3vw;
                }

                @media (max-width:450px) {
                    bottom: -5vw;
                    padding: 3vw 5vw;
                    font-size: 3.5vw;
                }
            }
        }
    }
}