// colors //

$dark: black;
$light: white;
$gold: #FFC619;
$primary: #001A49;
$dark-success: #134E42;
$light-success: rgba(30, 167, 139, 0.2);

.FormModal {
    top: 0;
    left: 0;
    z-index: 6;
    width: 100%;
    height: 100%;
    display: flex;
    position: fixed;
    align-items: center;
    justify-content: center;

    .child {
        width: 40%;
        z-index: 6;
        display: flex;
        position: sticky;
        flex-direction: column;
        background-color: $light;

        @media (max-width:1000px) {
            width: 50%;
        }

        @media (max-width:767px) {
            width: 70%;
        }

        @media (max-width:450px) {
            width: 80%;
        }

        .label {
            opacity: 0.8;
        }

        .input {
            width: 100%;
            outline: none;
            background-color: $light;
            border: solid 1px $dark-success;

            &:focus {
                border-color: $dark;
            }
        }

        .phone-num {
            display: flex;
            position: relative;
            align-items: center;
            justify-content: flex-start;

            .tel-code {
                left: 0.5vw;
                position: absolute;

                @media (max-width:1000px) {
                    left: 1vw;
                }

                @media (max-width:767px) {
                    left: 1.5vw;
                }

                @media (max-width:450px) {
                    left: 2vw;
                }
            }

            .input {
                padding-left: 4vw;

                @media (max-width:1000px) {
                    padding-left: 6vw;
                }

                @media (max-width:767px) {
                    padding-left: 8vw;
                }

                @media (max-width:450px) {
                    padding-left: 11vw;
                }
            }
        }

        .jins {
            display: flex;
            align-items: center;

            .radio {
                width: 1.5vw;
                height: 1.5vw;
                cursor: pointer;

                @media (max-width:767px) {
                    width: 3vw;
                    height: 3vw;
                }

                @media (max-width:450px) {
                    width: 5vw;
                    height: 5vw;
                }
            }

            label {
                cursor: pointer;
            }
        }

        .pay-btn {
            border: none;
            color: $light;
            background-color: $dark-success;
        }

        .loading-btn {
            opacity: 0.8;
            display: flex;
            overflow: hidden;
            position: relative;
            cursor: not-allowed;
            align-items: center;
            justify-content: center;

            &::before {
                top: 0;
                left: 0;
                z-index: 2;
                content: "";
                width: 100%;
                height: 100%;
                position: absolute;
                background-color: $dark-success;
            }

            &::after {
                z-index: 3;
                content: "";
                width: 1.4vw;
                height: 1.4vw;
                position: absolute;
                border-radius: 50%;
                -webkit-border-radius: 50%;
                -moz-border-radius: 50%;
                -ms-border-radius: 50%;
                -o-border-radius: 50%;
                animation-name: load-btn;
                animation-duration: 1.3s;
                border: solid 3px $light;
                border-top-color: transparent;
                border-left-color: transparent;
                animation-timing-function: linear;
                animation-iteration-count: infinite;

                @media (max-width:1000px) {
                    width: 2.2vw;
                    height: 2.2vw;
                }

                @media (max-width:767px) {
                    width: 3vw;
                    height: 3vw;
                }

                @media (max-width:450px) {
                    width: 4vw;
                    height: 4vw;
                    border-width: 2px;
                }
            }

            @keyframes load-btn {
                100% {
                    transform: rotate(360deg);
                    -webkit-transform: rotate(360deg);
                    -moz-transform: rotate(360deg);
                    -ms-transform: rotate(360deg);
                    -o-transform: rotate(360deg);
                }
            }
        }
    }
}