// colors //

$dark: black;
$light: white;
$gold: #FFC619;
$primary: #001A49;
$dark-success: #134E42;
$light-success: rgba(30, 167, 139, 0.2);

.Courses {
    padding-top: 8vw !important;

    @media (max-width:1000px) {
        padding-top: 10vw !important;
    }

    @media (max-width:767px) {
        padding-top: 16vw !important;
    }

    @media (max-width:450px) {
        padding-top: 20vw !important;
    }

    .wrapper {
        display: flex;
        flex-direction: column;

        iframe {
            max-width: 100%;
            min-width: 100%;
            max-height: 40vw;
            min-height: 40vw;

            @media (max-width:767px) {
                max-height: 50vw;
                min-height: 50vw;
            }
        }

        .texts {
            display: flex;
            padding: 0 11vw;
            flex-direction: column;

            @media (max-width:767px) {
                padding: 0;
            }

            .big-text {
                font-weight: 400;
                color: $dark-success;
                font-family: mulish400;
            }
        }

        .bottom-text {
            overflow: hidden;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 4;
        }

        .show-text {
            -webkit-line-clamp: 1000;
        }

        .w-cont {
            display: grid;
            align-items: center;
            grid-template-columns: repeat(2, 2fr);

            @media (max-width:767px) {
                grid-template-columns: repeat(1, 1fr);
            }

            .left {
                display: flex;
                flex-direction: column;

                .big-text {
                    line-height: 2.5vw;

                    @media (max-width:767px) {
                        line-height: 5vw;
                    }
                }
            }

            .person-img {
                max-width: 100%;
                min-width: 100%;
            }
        }

        .fd-column {
            display: flex;
            flex-direction: column;
        }

        .btns {
            display: flex;
            align-items: center;
            flex-direction: column;
        }

        .explore {
            gap: 5px;
            border: none;
            outline: none;
            display: flex;
            align-items: center;
            text-decoration: none;
            justify-content: center;
            background-color: $light;
            font-weight: 700 !important;
            color: $dark-success !important;
            font-family: mulish700 !important;
            box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;

            .icon {
                width: 0.7vw;
                display: flex;
                margin-top: 3px;
                fill: $dark-success;
                align-items: center;
                justify-content: center;

                @media (max-width:767px) {
                    width: 1.6vw;
                }

                @media (max-width:450px) {
                    width: 2vw;
                }
            }

            .up-icon {
                transform: rotate(180deg);
                -webkit-transform: rotate(180deg);
                -moz-transform: rotate(180deg);
                -ms-transform: rotate(180deg);
                -o-transform: rotate(180deg);
            }

        }

        .explore-bg {
            gap: 5px;
            border: none;
            outline: none;
            display: flex;
            align-items: center;
            text-decoration: none;
            justify-content: center;
            color: $light !important;
            font-weight: 700 !important;
            background-color: $dark-success;
            font-family: mulish700 !important;

            .icon {
                width: 0.7vw;
                display: flex;
                margin-top: 3px;
                fill: $dark-success;
                align-items: center;
                justify-content: center;
            }

            .up-icon {
                transform: rotate(180deg);
            }

        }

        .glass-anim {
            display: flex;
            overflow: hidden;
            position: relative;
            align-items: center;
            justify-content: center;

            &::before {
                left: -8vw;
                width: 4vw;
                content: "";
                height: 200%;
                position: absolute;
                animation-name: glass;
                animation-duration: 2s;
                transform: rotate(30deg);
                -webkit-transform: rotate(30deg);
                -moz-transform: rotate(30deg);
                -ms-transform: rotate(30deg);
                -o-transform: rotate(30deg);
                filter: blur(1px) brightness(1.3);
                animation-iteration-count: infinite;
                -webkit-filter: blur(1px) brightness(1.3);
                background-color: rgba(255, 255, 255, 0.39);
            }

            @keyframes glass {
                100% {
                    left: calc(100% + 8vw);
                }
            }
        }

        .feedback-img {
            max-width: 100%;
            min-width: 100%;
        }

        .anim-opacity {
            opacity: 1;
            animation-name: anim;
            animation-duration: 1.5s;
            animation-iteration-count: infinite;
        }

        @keyframes anim {
            0% {
                opacity: 1;
            }

            50% {
                opacity: 0.3;
            }

            100% {
                opacity: 1;
            }
        }
    }

    .get-bg {
        background-color: $dark-success;

        .texts {
            .big-text {
                color: $light;
                font-weight: 400;
                font-family: mulish400;
            }
        }
    }
}