// colors //

$dark: black;
$light: white;
$gold: #FFC619;
$primary: #001A49;
$dark-success: #134E42;
$light-success: rgba(30, 167, 139, 0.2);

.TeacherVideos {
    .wrapper {
        display: flex;
        flex-direction: column;

        .file-img {
            top: -9vw;
            right: 3vw;
            height: 15vw;
            position: absolute;
        }

        .border-img {
            top: 4vw;
            right: 17vw;
            width: 21vw;
            position: absolute;

            @media (max-width:1000px) {
                display: none;
            }
        }

        .sub-title {
            gap: 1vw;
            display: flex;
            text-align: left;
            font-weight: 700;
            align-items: center;
            font-family: mulish700;

            .play-btn {
                max-width: 6.5vw;
                min-width: 6.5vw;
            }
        }

        .mySwiper {
            width: 100%;
            padding: 0 20px;

            @media (max-width:767px) {
                padding: 0 15px;
            }

            .video {
                height: 15vw;
                overflow: hidden;
                background-color: $light-success;

                @media (max-width:1000px) {
                    height: 20vw;
                }

                @media (max-width:767px) {
                    height: 40vw;
                }

                @media (max-width:450px) {
                    height: 56vw;
                }

                iframe {
                    max-width: 100%;
                    min-width: 100%;
                    max-height: 100%;
                    min-height: 100%;
                    object-fit: cover;
                }
            }

            .swiper-button-next,
            .swiper-button-prev {
                right: 0;
                width: 3vw;
                height: 3vw;
                display: flex;
                border-radius: 50%;
                -webkit-border-radius: 50%;
                -moz-border-radius: 50%;
                -ms-border-radius: 50%;
                -o-border-radius: 50%;
                align-items: center;
                justify-content: center;
                background-color: $light;
                border: solid 2px rgb(43, 43, 43);

                &:hover {
                    opacity: 0.8;
                }

                &::after {
                    font-size: 1.5vw;
                    color: rgb(43, 43, 43);

                    @media (max-width:767px) {
                        font-size: 2.5vw;
                    }
                }

                @media (max-width:767px) {
                    width: 6vw;
                    height: 6vw;
                    padding: 1vw;
                    border-width: 1px;
                }

                @media (max-width:450px) {
                    width: 7vw;
                    height: 7vw;
                    padding: 1vw;
                    border-width: 1px;
                }
            }

            .swiper-button-prev {
                left: 0;
                right: auto;
            }
        }
    }
}