// colors //

$dark: black;
$light: white;
$gold: #FFC619;
$primary: #001A49;
$dark-success: #134E42;
$light-success: rgba(30, 167, 139, 0.2);

.Modal {
    top: 0;
    left: 0;
    z-index: 6;
    width: 100%;
    height: 100%;
    display: flex;
    position: fixed;
    align-items: center;
    justify-content: center;

    .child {
        z-index: 6;
        display: flex;
        max-width: 40%;
        position: sticky;
        align-items: center;
        flex-direction: column;
        background-color: $light;

        @media (max-width:767px) {
            max-width: 60%;
        }

        @media (max-width:450px) {
            max-width: 70%;
        }

        .sub-title {
            font-weight: 700;
            text-align: center;
            color: $dark-success;
            font-family: mulish700;
        }

        .ok-btn {
            border: none;
            color: $light;
            cursor: pointer;
            font-weight: 700;
            padding: 1vw 4vw;
            font-family: mulish700;
            background-color: $dark-success;

            @media (max-width:450px) {
                padding: 1.5vw 6vw;
            }
        }
    }
}