// colors //

$dark: black;
$light: white;
$gold: #FFC619;
$primary: #001A49;
$dark-success: #134E42;
$light-success: rgba(30, 167, 139, 0.2);

.Infographics {
    background-color: $dark-success;

    .file-img {
        right: 3vw;
        bottom: -6vw;
        height: 15vw;
        position: absolute;
    }

    .wrapper {
        display: flex;
        align-items: center;
        flex-direction: column;

        .logo {
            max-width: 13vw;
            min-width: 13vw;

            @media (max-width:767px) {
                max-width: 19vw;
                min-width: 19vw;
            }

            @media (max-width:450px) {
                max-width: 25vw;
                min-width: 25vw;
            }
        }

        .infos {
            display: grid;
            padding-left: 0;
            padding-right: 0;
            grid-template-columns: repeat(3, 3fr);

            @media (max-width:450px) {
                grid-template-columns: repeat(1, 1fr);
            }

            .info {
                display: flex;
                cursor: pointer;
                overflow: hidden;
                position: relative;
                padding-top: 2.5vw;
                align-items: center;
                padding-bottom: 2.5vw;
                flex-direction: column;
                background-color: rgba(255, 255, 255, 0.152);
                border-top: solid 1px rgba(255, 255, 255, 0.568);
                border-left: solid 1px rgba(255, 255, 255, 0.568);
                backdrop-filter: blur(13px);

                &:hover {
                    &::before {
                        width: 46vw;
                        height: 46vw;

                        @media (max-width:450px) {
                            width: 0;
                            height: 0;
                        }
                    }

                    .sub-title,
                    .text {
                        color: $dark-success;

                        @media (max-width:450px) {
                            color: $light;
                        }
                    }
                }

                &::before {
                    width: 0;
                    height: 0;
                    top: -13vw;
                    left: -13vw;
                    content: "";
                    transition: 0.3s;
                    -webkit-transition: 0.3s;
                    -moz-transition: 0.3s;
                    -ms-transition: 0.3s;
                    -o-transition: 0.3s;
                    position: absolute;
                    border-radius: 50%;
                    -webkit-border-radius: 50%;
                    -moz-border-radius: 50%;
                    -ms-border-radius: 50%;
                    -o-border-radius: 50%;
                    background-color: rgba(255, 255, 255, 0.887);
                }

                @media (max-width:450px) {
                    border: none;
                    background-color: transparent;
                }

                .sub-title {
                    z-index: 2;
                    color: $light;
                    position: sticky;
                    font-weight: 700;
                    transition: 0.3s;
                    -webkit-transition: 0.3s;
                    -moz-transition: 0.3s;
                    -ms-transition: 0.3s;
                    -o-transition: 0.3s;
                    text-align: center;
                    font-family: mulish700;
                }

                .text {
                    z-index: 2;
                    opacity: 0.7;
                    color: $light;
                    position: sticky;
                    transition: 0.3s;
                    -webkit-transition: 0.3s;
                    -moz-transition: 0.3s;
                    -ms-transition: 0.3s;
                    -o-transition: 0.3s;
                    text-align: center;
                }
            }
        }
    }
}