// colors //

$dark: black;
$light: white;
$dark-success: #134E42;
$light-success: rgba(30, 167, 139, 0.2);

.Header {
    .texture {
        top: 15vw;
        z-index: 0;
        left: 10vw;
        width: 18vw;
        height: 18vw;
        opacity: 0.5;
        position: absolute;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
        background: #94FFE9;
        filter: blur(190px);
        -webkit-filter: blur(190px);
    }

    .texture1 {
        top: 10vw;
        z-index: 0;
        right: 10vw;
        width: 18vw;
        height: 18vw;
        opacity: 0.5;
        position: absolute;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
        background: #94FFE9;
        filter: blur(190px);
        -webkit-filter: blur(190px);
    }

    .texture2 {
        top: 20vw;
        left: 40vw;
        width: 15vw;
        height: 15vw;
        opacity: 0.5;
        position: absolute;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
        background: #94FFE9;
        filter: blur(190px);
        -webkit-filter: blur(190px);
    }

    .wrapper {
        display: flex;
        padding-top: 10vw;
        flex-direction: column;

        @media (max-width:1000px) {
            padding-top: 17vw;
        }

        @media (max-width:450px) {
            padding-top: 20vw;
        }

        .mySwiper {
            width: 100%;

            @media (max-width:767px) {
                padding-bottom: 4vw;
            }

            .course {
                display: flex;
                flex-direction: column;

                @media (max-width:767px) {
                    gap: 6vw;
                }

                .top {
                    display: flex;
                    justify-content: space-between;

                    @media (max-width:767px) {
                        gap: 2vw;
                        align-items: center;
                        flex-direction: column-reverse;
                    }
                }

                .left {
                    width: 55%;
                    display: flex;
                    padding-left: 4vw;
                    flex-direction: column;
                    align-items: flex-start;

                    @media (max-width:767px) {
                        width: 85%;
                    }

                    .explore {
                        display: flex;
                        color: $light;
                        font-weight: 700;
                        align-items: center;
                        font-family: mulish700;
                        background-color: $dark-success;

                        .arrow-icon {
                            width: 1.7vw;
                            fill: $light;
                            display: flex;
                            align-items: center;
                            justify-content: center;

                            @media (max-width:767px) {
                                width: 3vw;
                            }
                        }
                    }
                }

                .right {
                    width: 45%;
                    display: flex;
                    position: relative;
                    justify-content: center;

                    @media (max-width:767px) {
                        width: 60%;

                        &::before {
                            left: 0;
                            bottom: 0;
                            width: 100%;
                            height: 5vw;
                            content: "";
                            position: absolute;
                            background: linear-gradient(180deg, rgba(255, 255, 255, 0), white);
                        }
                    }

                    .img {
                        max-width: 80%;
                        min-width: 80%;
                    }
                }
            }

            .swiper-button-prev,
            .swiper-button-next {
                top: 40%;
                right: 0;
                width: 3vw;
                height: 3vw;
                opacity: 0.8;
                display: flex;
                padding: 0.5vw;
                border-radius: 50%;
                -webkit-border-radius: 50%;
                -moz-border-radius: 50%;
                -ms-border-radius: 50%;
                -o-border-radius: 50%;
                align-items: center;
                justify-content: center;
                border: solid 2px $dark;

                &:hover {
                    background-color: rgba(255, 255, 255, 0.557);
                }

                &::before {
                    width: 100%;
                    content: "";
                    height: 100%;
                    background: url(../../assets/images/right-arrow.svg) no-repeat;
                    background-size: 100% 100%;
                }

                &::after {
                    display: none;
                }

                @media (max-width:767px) {
                    width: 6vw;
                    height: 6vw;
                    padding: 1vw;
                    border-width: 1px;
                }

                @media (max-width:450px) {
                    width: 7vw;
                    height: 7vw;
                    padding: 1vw;
                    border-width: 1px;
                }
            }

            .swiper-button-prev {
                &::before {
                    transform: rotate(180deg);
                    -webkit-transform: rotate(180deg);
                    -moz-transform: rotate(180deg);
                    -ms-transform: rotate(180deg);
                    -o-transform: rotate(180deg);
                }
            }
        }

        .bottom {
            width: 100%;
            display: grid;
            margin-top: -1.5vw;
            position: relative;
            background-color: $light;
            grid-template-columns: repeat(3, 3fr);
            box-shadow: 0px 19px 82px rgba(155, 255, 235, 0.5);

            &::before {
                left: 0;
                top: -1vw;
                z-index: -1;
                content: "";
                width: 100%;
                height: 100%;
                position: absolute;
                border-radius: 1vw;
                -webkit-border-radius: 1vw;
                -moz-border-radius: 1vw;
                -ms-border-radius: 1vw;
                -o-border-radius: 1vw;
                background-color: $dark-success;

                @media (max-width:767px) {
                    top: -2vw;
                    border-radius: 2.5vw;
                    -webkit-border-radius: 2.5vw;
                    -moz-border-radius: 2.5vw;
                    -ms-border-radius: 2.5vw;
                    -o-border-radius: 2.5vw;
                }
            }

            @media (max-width:1000px) {
                margin-top: -2.3vw;
            }

            @media (max-width:767px) {
                margin-top: 0;
                box-shadow: 0px 5px 10px rgba(155, 255, 235, 0.5);
            }

            .tool {
                display: flex;
                align-items: center;

                @media (max-width:767px) {
                    flex-direction: column;
                }

                .icon {
                    width: 5vw;
                    height: 4vw;
                    padding: 1vw;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    background: $light-success;

                    @media (max-width:767px) {
                        padding: 3vw;
                        width: 10vw;
                        height: 10vw;
                    }

                    .ic-img {
                        max-width: 100%;
                        min-width: 100%;
                    }
                }

                .text {
                    @media (max-width:767px) {
                        text-align: center;
                    }

                    @media (max-width:450px) {
                        font-size: 2.5vw;
                    }
                }
            }
        }
    }
}