// colors //

$dark: black;
$light: white;
$primary: #001A49;
$dark-success: #134E42;
$light-success: rgba(30, 167, 139, 0.2);

.FAQ {
    .wrapper {
        display: flex;
        align-items: center;
        flex-direction: column;

        .sub-title {
            font-weight: 700;
            text-align: center;
            font-family: mulish700;
        }

        .cards {
            width: 90%;
            display: flex;
            flex-direction: column;

            @media (max-width:767px) {
                width: 100%;
            }

            .question-bar {
                width: 100%;
                display: flex;
                flex-direction: column;
                border: solid 1px $light-success;

                .question {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;

                    .big-text {
                        width: 85%;
                        color: $primary;
                        font-weight: 500;
                        font-family: mulish700;
                    }

                    .icon {
                        width: 3vw;
                        height: 3vw;
                        padding: 1vw;
                        display: flex;
                        cursor: pointer;
                        transition: 0.4s;
                        -webkit-transition: 0.4s;
                        -moz-transition: 0.4s;
                        -ms-transition: 0.4s;
                        -o-transition: 0.4s;
                        border-radius: 50%;
                        -webkit-border-radius: 50%;
                        -moz-border-radius: 50%;
                        -ms-border-radius: 50%;
                        -o-border-radius: 50%;
                        fill: $dark-success;
                        align-items: center;
                        justify-content: center;
                        background-color: $light-success;

                        @media (max-width:767px) {
                            width: 5vw;
                            height: 5vw;
                            padding: 1.5vw;
                        }

                        @media (max-width:450px) {
                            width: 7vw;
                            height: 7vw;
                            padding: 2vw;
                        }

                        .icn-img {
                            max-width: 100%;
                            min-width: 100%;
                        }
                    }

                    .active-icon {
                        transform: rotate(180deg);
                        -webkit-transform: rotate(180deg);
                        -moz-transform: rotate(180deg);
                        -ms-transform: rotate(180deg);
                        -o-transform: rotate(180deg);
                    }
                }

                .answer {
                    opacity: 0.6;
                    max-height: 1px;
                    color: $primary;
                    transition: 0.4s;
                    -webkit-transition: 0.4s;
                    -moz-transition: 0.4s;
                    -ms-transition: 0.4s;
                    -o-transition: 0.4s;
                    overflow: hidden;
                }

                .active-answer {
                    transition: 3s;
                    -webkit-transition: 3s;
                    -moz-transition: 3s;
                    -ms-transition: 3s;
                    -o-transition: 3s;
                    max-height: 100vw;

                    @media(max-width:1000px) {
                        transition: 2s;
                        -webkit-transition: 2s;
                        -moz-transition: 2s;
                        -ms-transition: 2s;
                        -o-transition: 2s;
                    }

                    @media(max-width:500px) {
                        max-height: 200vw;
                    }
                }
            }
        }
    }
}