// colors //

$dark: black;
$light: white;
$primary: #001A49;
$dark-success: #134E42;
$light-success: rgba(30, 167, 139, 0.2);

.Advantages {
    .texture {
        top: 15vw;
        z-index: 0;
        left: 10vw;
        width: 18vw;
        height: 18vw;
        opacity: 0.5;
        position: absolute;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
        background: #94FFE9;
        filter: blur(190px);
        -webkit-filter: blur(190px);
    }

    .texture1 {
        top: 10vw;
        z-index: 0;
        right: 10vw;
        width: 18vw;
        height: 18vw;
        opacity: 0.5;
        position: absolute;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
        background: #94FFE9;
        filter: blur(190px);
        -webkit-filter: blur(190px);
    }

    .texture2 {
        top: 20vw;
        left: 40vw;
        width: 15vw;
        height: 15vw;
        opacity: 0.5;
        position: absolute;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
        background: #94FFE9;
        filter: blur(190px);
        -webkit-filter: blur(190px);
    }

    .border-img1 {
        top: 10vw;
        left: 9vw;
        height: 18vw;
        position: absolute;

        @media (max-width:1000px) {
            left: 3vw;
            top: 12vw;
        }

        @media (max-width:767px) {
            display: none;
        }
    }

    .border-img2 {
        top: 28vw;
        width: 38vw;
        right: 10vw;
        position: absolute;

        @media (max-width:1000px) {
            top: 32vw;
            right: 10vw;
        }

        @media (max-width:767px) {
            display: none;
        }
    }

    .border-img3 {
        top: 35vw;
        left: 12vw;
        width: 35vw;
        position: absolute;

        @media (max-width:1000px) {
            top: 42vw;
            left: 7vw;
        }

        @media (max-width:767px) {
            display: none;
        }
    }

    .border-img4 {
        top: 45vw;
        right: 37vw;
        width: 16vw;
        position: absolute;

        @media (max-width:767px) {
            display: none;
        }
    }

    .wrapper {
        display: flex;
        flex-direction: column;

        .name {
            font-weight: 500;
            font-family: mulish400;
        }

        .sub-title {
            font-weight: 700;
            font-family: mulish700;
        }

        .slog {
            opacity: 0.7;
        }

        .infos {
            gap: 1vw 2vw;
            display: grid;
            grid-template-columns: repeat(2, 2fr);

            @media (max-width:767px) {
                gap: 3vw;
                margin-top: 4vw;
                grid-template-columns: repeat(1, 1fr);
            }

            @media (max-width:450px) {
                gap: 5vw;
            }

            .tool {
                display: flex;
                align-items: center;

                .icon-img {
                    max-width: 6.5vw;
                    min-width: 6.5vw;

                    @media (max-width:1000px) {
                        max-width: 7.5vw;
                        min-width: 7.5vw;
                    }

                    @media (max-width:767px) {
                        max-width: 10vw;
                        min-width: 10vw;
                    }

                    @media (max-width:450px) {
                        max-width: 14vw;
                        min-width: 14vw;
                    }
                }

                .texts {
                    display: flex;
                    flex-direction: column;

                    .big-text {
                        font-weight: 700;
                        font-family: mulish700;
                    }

                    .min-text {
                        opacity: 0.8;
                        line-height: 1.5vw;

                        @media (max-width:767px) {
                            line-height: 3vw;
                        }

                        @media (max-width:450px) {
                            line-height: 4vw;
                        }
                    }
                }
            }

            .res {
                @media (max-width:767px) {
                    display: none;
                }
            }
        }

        .btns {
            display: flex;
            align-items: center;
            justify-content: flex-start;

            .explore {
                color: $light;
                font-weight: 700;
                padding: 1vw 2vw;
                font-family: mulish700;
                border: solid 1px $dark-success;
                background-color: $dark-success;

                &:last-child {
                    color: $dark-success;
                    background-color: $light;
                }

                @media (max-width:767px) {
                    padding: 2vw 4vw;
                }
            }
        }
    }
}