// colors //

$dark: black;
$light: white;
$gold: #FFC619;
$primary: #001A49;
$dark-success: #134E42;
$secondary: rgb(216, 216, 216);
$light-secondary: rgb(246, 246, 246);
$light-success: rgba(30, 167, 139, 0.2);

.Payment {
    padding-top: 8vw !important;
    background-color: rgb(249, 249, 249);

    @media (max-width:1000px) {
        padding-top: 10vw !important;
    }

    @media (max-width:767px) {
        padding-top: 16vw !important;
    }

    @media (max-width:450px) {
        padding-top: 20vw !important;
    }

    .wrapper {
        display: flex;
        padding-left: 10vw;
        padding-right: 10vw;
        flex-direction: column;

        @media (max-width:1000px) {
            padding-left: 5vw;
            padding-right: 5vw;
        }

        .payment-bar {
            display: flex;
            justify-content: space-between;

            @media (max-width:767px) {
                gap: 4vw;
                flex-direction: column;
                justify-content: flex-start;
            }

            @media (max-width:450px) {
                gap: 6vw;
            }

            .left {
                width: 55%;
                display: flex;
                flex-direction: column;
                background-color: $light;
                box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;

                @media (max-width:767px) {
                    width: 100%;
                }

                .price-card {
                    display: flex;
                    cursor: pointer;
                    align-items: center;
                    border: solid 1px $secondary;
                    background-color: $light-secondary;

                    &:hover {
                        background-color: $light-success;
                    }

                    .checked {
                        width: 1.7vw;
                        height: 1.7vw;
                        display: flex;
                        border-radius: 50%;
                        -webkit-border-radius: 50%;
                        -moz-border-radius: 50%;
                        -ms-border-radius: 50%;
                        -o-border-radius: 50%;
                        align-items: center;
                        justify-content: center;
                        border: solid 1px $dark-success;

                        @media (max-width:1000px) {
                            width: 2.5vw;
                            height: 2.5vw;
                        }

                        @media (max-width:767px) {
                            width: 3.5vw;
                            height: 3.5vw;
                        }

                        @media (max-width:450px) {
                            width: 4.5vw;
                            height: 4.5vw;
                        }

                        .icon {
                            width: 75%;
                            display: flex;
                            stroke: transparent;
                            align-items: center;
                            justify-content: center;
                        }
                    }

                    .active-checked {
                        background-color: $dark-success;

                        .icon {
                            stroke: $light;
                        }
                    }
                }

                .result-price {
                    display: flex;
                    justify-content: space-between;
                }
            }

            .right {
                width: 40%;
                display: flex;
                flex-direction: column;
                background-color: $light;
                justify-content: flex-start;
                box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;

                @media (max-width:767px) {
                    width: 100%;
                }

                .payment-types {
                    display: grid;
                    grid-template-columns: repeat(3, 3fr);

                    @media (max-width:767px) {
                        gap: 4vw;
                    }

                    .type-card {
                        .type-label {
                            height: 5.5vw;
                            display: flex;
                            cursor: pointer;
                            align-items: center;
                            justify-content: center;
                            border: solid 1px $secondary;
                            background-color: $light-secondary;

                            &:hover {
                                box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
                            }

                            @media (max-width:767px) {
                                height: 15vw;
                            }

                            .logo {
                                max-width: 100%;
                                min-width: 100%;
                            }
                        }

                        input[type="radio"] {
                            display: none;
                        }

                        input[type="radio"]:checked~label {
                            border: solid 2px $dark-success;
                            background-color: rgb(223, 223, 223);
                            box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
                        }
                    }
                }

                .forms {
                    display: flex;
                    flex-direction: column;

                    .label {
                        opacity: 0.8;
                    }

                    .input {
                        width: 100%;
                        outline: none;
                        background-color: $light;
                        border: solid 1px $dark-success;

                        &:focus {
                            border-color: $dark;
                        }
                    }

                    .phone-num {
                        display: flex;
                        position: relative;
                        align-items: center;
                        justify-content: flex-start;

                        .tel-code {
                            left: 0.5vw;
                            position: absolute;

                            @media (max-width:1000px) {
                                left: 1vw;
                            }

                            @media (max-width:767px) {
                                left: 1.5vw;
                            }

                            @media (max-width:450px) {
                                left: 2vw;
                            }
                        }

                        .input {
                            padding-left: 4vw;

                            @media (max-width:1000px) {
                                padding-left: 6vw;
                            }

                            @media (max-width:767px) {
                                padding-left: 8vw;
                            }

                            @media (max-width:450px) {
                                padding-left: 11vw;
                            }
                        }
                    }

                    .pay-btn {
                        border: none;
                        color: $light;
                        background-color: $dark-success;
                    }

                    .loading-btn {
                        opacity: 0.8;
                        display: flex;
                        overflow: hidden;
                        position: relative;
                        cursor: not-allowed;
                        align-items: center;
                        justify-content: center;

                        &::before {
                            top: 0;
                            left: 0;
                            z-index: 2;
                            content: "";
                            width: 100%;
                            height: 100%;
                            position: absolute;
                            background-color: $dark-success;
                        }

                        &::after {
                            z-index: 3;
                            content: "";
                            width: 1.4vw;
                            height: 1.4vw;
                            position: absolute;
                            border-radius: 50%;
                            -webkit-border-radius: 50%;
                            -moz-border-radius: 50%;
                            -ms-border-radius: 50%;
                            -o-border-radius: 50%;
                            animation-name: load-btn;
                            animation-duration: 1.3s;
                            border: solid 3px $light;
                            border-top-color: transparent;
                            border-left-color: transparent;
                            animation-timing-function: linear;
                            animation-iteration-count: infinite;

                            @media (max-width:1000px) {
                                width: 2.2vw;
                                height: 2.2vw;
                            }

                            @media (max-width:767px) {
                                width: 3vw;
                                height: 3vw;
                            }

                            @media (max-width:450px) {
                                width: 4vw;
                                height: 4vw;
                                border-width: 2px;
                            }
                        }

                        @keyframes load-btn {
                            100% {
                                transform: rotate(360deg);
                                -webkit-transform: rotate(360deg);
                                -moz-transform: rotate(360deg);
                                -ms-transform: rotate(360deg);
                                -o-transform: rotate(360deg);
                            }
                        }
                    }
                }

                .payme-btn {
                    width: 100%;
                    border: none;
                    color: $light;
                    // display: flex;
                    align-items: center;
                    justify-content: center;
                    background-color: $dark-success;
                }
            }
        }
    }
}