// colors //

$dark: black;
$light: white;
$dark-success: #134E42;
$light-success: rgba(30, 167, 139, 0.2);

.Footer {
    background-color: $dark-success;

    .wrapper {
        display: flex;
        flex-direction: column;

        .top {
            display: flex;
            align-items: center;
            justify-content: space-between;

            @media (max-width:450px) {
                gap: 2vw;
                flex-direction: column;
                align-items: flex-start;
            }

            .logo {
                width: 12vw;

                @media (max-width:1000px) {
                    width: 16vw;
                }

                @media (max-width:767px) {
                    width: 20vw;
                }

                @media (max-width:450px) {
                    width: 30vw;
                }

                .logo-img {
                    max-width: 100%;
                    min-width: 100%;
                }
            }

            .forms {
                width: 75%;
                display: flex;
                position: relative;
                align-items: center;

                @media (max-width:450px) {
                    width: 100%;
                    display: none;
                }

                .input {
                    width: 100%;
                    outline: none;
                    padding-right: 7vw;
                    color: $dark-success;
                    background-color: $light;
                    border: solid 1px $light;

                    &::placeholder {
                        color: $dark-success;
                    }

                    &:focus {
                        border-color: $dark;
                    }

                    @media (max-width:1000px) {
                        padding-right: 11vw;
                    }

                    @media (max-width:767px) {
                        padding-right: 13vw;
                    }

                    @media (max-width:450px) {
                        padding-right: 19vw;
                    }
                }

                .submit-btn {
                    right: 0.4vw;
                    border: none;
                    color: $light;
                    cursor: pointer;
                    font-weight: 600;
                    padding: 0.7vw 1vw;
                    position: absolute;
                    font-family: mulish700;
                    background-color: $dark-success;

                    @media (max-width:1000px) {
                        right: 0.6vw;
                        padding: 1vw 1.3vw;
                    }

                    @media (max-width:767px) {
                        padding: 1.3vw 1.7vw;
                    }

                    @media (max-width:450px) {
                        right: 0.9vw;
                        padding: 1.8vw 2.5vw;
                    }
                }
            }
        }

        .bottom {
            display: grid;
            grid-template-columns: repeat(3, 3fr);

            @media (max-width:450px) {
                gap: 2vw;
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
            }

            .tool {
                display: flex;
                flex-direction: column;
                align-items: flex-start;

                .text {
                    color: $light;
                    font-weight: 600;
                    font-family: mulish700;
                }

                .link {
                    opacity: 0.7;
                    font-weight: 400;
                    text-decoration: none;
                    font-family: mulish400;

                    &:hover {
                        text-decoration: underline;
                    }
                }
            }

            .c1 {
                @media (max-width:450px) {
                    width: 20%;
                }
            }

            .c2 {
                @media (max-width:450px) {
                    width: 35%;
                }
            }

            .c3 {
                @media (max-width:450px) {
                    width: 35%;
                }
            }
        }
    }
}