// colors //

$dark: black;
$light: white;
$dark-success: #134E42;
$light-secondary: #F5F5F5;
$light-success: rgba(30, 167, 139, 0.2);

.Navbar {
    top: 0;
    left: 0;
    z-index: 4;
    width: 100vw;
    position: fixed;
    background-color: $light;

    @media (max-width:767px) {
        box-shadow: 0 1px 5px 1px rgb(222, 222, 222);
    }

    .top-navbar {
        display: flex;
        padding: 0.5vw 10vw;
        align-items: center;
        justify-content: space-between;
        background-color: $light-secondary;

        @media (max-width:1000px) {
            padding: 0.5vw 5vw;
        }

        @media (max-width:767px) {
            padding: 1vw 5vw;
        }

        .welcome {
            position: relative;

            @media (max-width:767px) {
                display: none;
            }

            &::after {
                top: 0;
                left: 0;
                content: "";
                width: 100%;
                height: 100%;
                position: absolute;
                background-color: $light-secondary;
                animation: typing 6s steps(30) infinite;
                -webkit-animation: typing 6s steps(30) infinite;
            }

            @keyframes typing {
                90% {
                    left: 100%;
                    margin: 0 -10px 0 10px;
                }

                100% {
                    left: 100%;
                    margin: 0 -10px 0 10px;
                }
            }
        }

        .tools {
            display: flex;
            align-items: center;

            @media (max-width:767px) {
                width: 100%;
                justify-content: space-between;
            }

            .tool {
                display: flex;
                align-items: center;

                &::after {
                    content: "";
                    opacity: 0.5;
                    width: 0.6vw;
                    height: 0.6vw;
                    border-radius: 50%;
                    -webkit-border-radius: 50%;
                    -moz-border-radius: 50%;
                    -ms-border-radius: 50%;
                    -o-border-radius: 50%;
                    background-color: $dark-success;

                    @media (max-width:767px) {
                        width: 1.2vw;
                        height: 1.2vw;
                    }

                    @media (max-width:450px) {
                        width: 1.7vw;
                        height: 1.7vw;
                    }
                }
            }

            .tool-btn {
                color: $light;
                cursor: pointer;
                padding: 0.6vw 0.8vw;
                background: linear-gradient(90deg, #186556 0%, #22CFAC 100%);

                @media (max-width:767px) {
                    padding: 1vw 1.5vw;
                }

                @media (max-width:450px) {
                    padding: 1.3vw 2vw;
                }
            }

            .language {
                display: flex;
                position: relative;
                justify-content: center;

                .lang-title {
                    gap: 5px;
                    display: flex;
                    font-weight: 600;
                    align-items: center;
                    font-family: mulish700;
                    justify-content: center;

                    @media (max-width:450px) {
                        gap: 3px;
                    }

                    .down-icon {
                        width: 0.7vw;
                        fill: $light;
                        display: flex;
                        align-items: center;
                        justify-content: center;

                        @media (max-width:767px) {
                            width: 1.5vw;
                        }

                        @media (max-width:450px) {
                            width: 2vw;
                        }
                    }
                }

                .lang-menu {
                    z-index: 6;
                    width: 100%;
                    display: flex;
                    overflow: hidden;
                    position: absolute;
                    flex-direction: column;
                    bottom: calc(-100% - 15px);
                    background: linear-gradient(90deg, #186556 0%, #22CFAC 100%);

                    @media (max-width:767px) {
                        bottom: calc(-100% - 18px);
                    }

                    .lang {
                        width: 100%;
                        color: $light;
                        padding: 3px 0;
                        cursor: pointer;
                        font-weight: 600;
                        text-align: center;
                        font-family: mulish700;

                        &:hover {
                            color: $dark-success;
                            background-color: $light;
                        }
                    }
                }
            }
        }
    }

    .bottom-nabar {
        display: flex;
        align-items: center;
        padding: 0.5vw 10vw;

        @media (max-width:1000px) {
            padding: 0.5vw 5vw;
        }

        @media (max-width:767px) {
            padding: 1vw 5vw;
            justify-content: space-between;
        }

        .logo {
            width: 10vw;

            @media (max-width:1000px) {
                width: 14vw;
            }

            @media (max-width:767px) {
                width: 20vw;
            }

            @media (max-width:450px) {
                width: 25vw;
            }

            .logo-img {
                max-width: 100%;
                min-width: 100%;
            }
        }

        .links {
            display: flex;

            @media (max-width:767px) {
                top: 0;
                width: 100vw;
                right: -100vw;
                height: 100vh;
                padding: 15vw 0;
                transition: 0.3s;
                -webkit-transition: 0.3s;
                -moz-transition: 0.3s;
                -ms-transition: 0.3s;
                -o-transition: 0.3s;
                position: absolute;
                align-items: center;
                flex-direction: column;
                justify-content: space-between;
                background-color: $dark-success;
            }

            .link {
                display: flex;
                position: relative;
                justify-content: center;

                &:hover {
                    &::after {
                        width: 90%;
                    }
                }

                &::after {
                    width: 0%;
                    height: 2px;
                    content: "";
                    bottom: -4px;
                    transition: 0.2s;
                    -webkit-transition: 0.2s;
                    -moz-transition: 0.2s;
                    -ms-transition: 0.2s;
                    -o-transition: 0.2s;
                    position: absolute;
                    border-radius: 15px;
                    -webkit-border-radius: 15px;
                    -moz-border-radius: 15px;
                    -ms-border-radius: 15px;
                    -o-border-radius: 15px;
                    background-color: $dark-success;
                }

                @media (max-width:767px) {
                    color: $light;
                }
            }

            .close-icon {
                top: 5vw;
                right: 7vw;
                width: 3.5vw;
                display: flex;
                position: absolute;
                align-items: center;

                @media (min-width:767.1px) {
                    display: none;
                }

                @media (max-width:450px) {
                    width: 4.5vw;
                }
            }
        }

        .show-links {
            right: 0;
        }

        .menu-icon {
            width: 4.5vw;
            display: flex;
            align-items: center;
            fill: $dark-success;
            justify-content: center;

            @media (min-width:767.1px) {
                display: none;
            }

            @media (max-width:450px) {
                width: 5.8vw;
            }
        }
    }
}